.plus {
  width: 15px;
  height: 15px;
  position: relative;
}
.plus::before {
  position: absolute;
  content: "";
  display: block;
  height: 1px;
  width: 100%;
  background: rgba(248, 253, 255, 0.7);
  top: 50%;
  left: 0;
  transform: rotate(90deg);
}

@font-face {
