}
.modal__textarea::placeholder {
  color: #ffffff;
  margin: 0;
}
.modal__inputs {
  display: flex;
  flex-direction: column;
  gap: 0;
}
.modal__input {
  background: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  border: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  width: 100%;
  font-family: "PTMono", "Arial", sans-serif;
  padding-top: 15px;
  padding-left: 15px;
  padding-bottom: 15px;
  margin-bottom: 15px;
  color: #ffffff;
}
.modal__input::placeholder {
  color: #ffffff;
}
.modal__button {
  margin-top: 25px;
  width: 100%;
  margin-right: 0;
  box-sizing: border-box;
  background: #041427;
