@media (min-resolution: 2dppx) {
  .form {
    background-image: url("../../../img/background-form.png");
  }
}
.form__bg {
  padding: 0 10px;
  display: flex;
  margin: 0 auto;
  max-width: 1220px;
  box-sizing: content-box;
}

.form__title {
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  font-family: "Roboto", "Arial", sans-serif;
  margin: 0;
}

.form__text {
  margin: 15px 0 35px 0;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: "PTMono", "Arial", sans-serif;
  padding-left: 5px;
}

.form__buttons {
  margin-top: 35px;
  display: flex;
  align-items: end;
}

.form__button {
  box-sizing: border-box;
  background: #041427;
  border-radius: 3px;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  font-family: "Roboto", "Arial", sans-serif;
  color: #ffffff;
  padding: 15px 25px;
  width: 265px;
  border: none;
  margin-right: 50px;
  height: 45px;
  cursor: pointer;
}
@media (hover: hover) {
  .form__button:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.form__button.focus-visible:focus {
  opacity: 0.8;
}

.form__textarea {
  background: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  border: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: "PTMono", "Arial", sans-serif;
  width: 100%;
  padding: 25px 0 0 15px;
  color: #ffffff;
}
.form__textarea::placeholder {
  color: #ffffff;
  margin: 0;
}

.form__inputs input {
  background: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  border: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  width: 100%;
  font-family: "PTMono", "Arial", sans-serif;
  padding-top: 15px;
  padding-left: 15px;
  padding-bottom: 15px;
  margin-bottom: 15px;
  color: #ffffff;
}
.form__inputs input::placeholder {
  color: #ffffff;
}

.form__inputs {
  display: flex;
  gap: 30px;
}

.form__approve {
  display: block;
  padding-left: 25px;
  width: 250px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  font-family: "PTMono", "Arial", sans-serif;
  opacity: 0.8;
  position: relative;
}
.form__approve::before {
  position: absolute;
  content: url("../../../img/svg/checkbox.svg");
  display: block;
  height: 15px;
  width: 15px;
  top: 15%;
  left: 0;
}

@media (max-width: 1130px) {
  .form__button {
    width: 176px;
  }
}
@media (max-width: 1023px) {
  .form__image {
    display: none;
  }
  .form__main {
    max-width: 670px;
  }
  .form__bg {
    max-width: 670px;
  }
  .form__inputs input {
    width: 320px;
  }
  .form {
    background-position: 100% 77%;
  }
}
@media (max-width: 767px) {
  .form__inputs input {
    width: 270px;
    margin-bottom: 20px;
  }
  .form__inputs {
    flex-direction: column;
    gap: 0;
  }
  .form__main {
    max-width: 270px;
    padding-top: 50px;
    padding-bottom: 90px;
    height: auto;
  }
  .form__bg {
    max-width: 270px;
  }
  .form__title {
    font-weight: 400;
    font-size: 30px;
    line-height: 140.62%;
  }
  .form__text {
    margin-bottom: 40px;
    margin-top: 20px;
  }
  .form__buttons {
    flex-direction: column;
  }
  .form__textarea {
    height: 180px;
  }
  .form__button {
    width: 100%;
    margin-right: 0;
    margin-bottom: 25px;
  }
  .form {
    background-position: 100% 77%;
  }
  .form__approve {
    width: 100%;
  }
}
.address__title {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: 20px;
  margin-top: 0;
}

.address__item {
  margin-bottom: 10px;
}

.address__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-family: "PTMono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
}

@media (max-width: 1023px) {
  .address__title {
    font-family: "Roboto", "Arial", sans-serif;
    font-weight: 400;
    font-size: 15px;
    line-height: 16px;
    text-transform: uppercase;
    margin-bottom: 20px;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .address__title {
    height: 60px;
    padding: 20px 25px;
    align-items: center;
    display: flex;
    position: relative;
    margin-bottom: 0;
    justify-content: space-between;
  }
  .address__title::after {
    position: absolute;
    content: "";
    display: block;
    height: 1px;
    width: 100%;
    background: rgba(248, 253, 255, 0.7);
    top: 0;
    left: 0;
  }
  .address__list {
    padding-left: 25px;
  }
  .address__item:last-child {
    margin-bottom: 20px;
  }
  .address__minus {
    width: 15px;
    height: 15px;
    position: relative;
  }
  .address__minus::after {
    position: absolute;
    content: "";
    display: block;
    height: 1px;
    width: 100%;
    background: rgba(248, 253, 255, 0.7);
    top: 50%;
    left: 0;
  }
}
.navigation {
  margin-right: 125px;
}

.navigation__title {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: 20px;
  margin-top: 0;
}

.navigation__list {
  font-family: "PTMono", "Arial", sans-serif;
  padding: 0;
  margin: 0;
  list-style-type: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
  display: flex;
}

.navigation__item {
  margin-bottom: 10px;
}

.navigation__left {
  margin-right: 50px;
}

@media (max-width: 1023px) {
  .navigation {
    margin-right: 30px;
  }
  .navigation__left {
    margin-right: 145px;
  }
  .navigation__item {
    margin-bottom: 10px;
  }
  .navigation__list {
    display: flex;
    flex-direction: column;
    font-family: "PTMono", "Arial", sans-serif;
    padding: 0;
    margin: 0;
    list-style-type: none;
    font-weight: 400;
    font-size: 16px;
    line-height: 135%;
    color: rgba(255, 255, 255, 0.7);
  }
  .navigation__title {
    margin-bottom: 20px;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .navigation__title {
    height: 60px;
    padding: 20px 25px;
    align-items: center;
    display: flex;
    justify-content: space-between;
    position: relative;
    margin-bottom: 0;
  }
  .navigation__title::after {
    position: absolute;
    content: "";
    display: block;
    height: 1px;
    width: 100%;
    background: rgba(248, 253, 255, 0.7);
    left: 0;
    top: 0;
  }
  .navigation {
    margin-right: 0;
  }
  .navigation__list {
    padding-left: 25px;
  }
  .navigation__item:last-child {
    margin-bottom: 20px;
  }
  .navigation__minus {
    width: 15px;
    height: 15px;
    position: relative;
  }
  .navigation__minus::after {
    position: absolute;
    content: "";
    display: block;
    height: 1px;
    width: 100%;
    background: rgba(248, 253, 255, 0.7);
    top: 50%;
    left: 0;
  }
}
@media (min-width: 1024px) {
  .socials__list {
    list-style-type: none;
    display: flex;
    margin: 0;
    padding-left: 10px;
  }
  .socials__item {
    width: 40px;
    height: 40px;
    align-items: center;
    display: flex;
    justify-content: center;
  }
  .socials__item:not(:last-child) {
    margin-right: 15px;
  }
  .socials {
    margin-right: 160px;
  }
}
@media (max-width: 1023px) {
  .socials__list {
    list-style-type: none;
    display: flex;
    padding-left: 10px;
    margin: 0;
  }
  .socials {
    display: flex;
    justify-content: space-between;
    margin-bottom: 35px;
    max-height: 55px;
  }
  .socials__item {
    width: 40px;
    height: 40px;
    align-items: center;
    display: flex;
    justify-content: center;
  }
  .socials__item:not(:last-child) {
    margin-right: 15px;
  }
}
@media (max-width: 767px) {
  .socials {
    flex-direction: column;
    max-height: 170px;
    margin: 0 auto;
    padding-bottom: 25px;
    width: 215px;
  }
  .socials__list {
    padding-left: 0;
    justify-content: center;
  }
}
